import { JSX, ComponentChildren } from 'preact';
import { useState, useEffect } from 'preact/hooks';
import '@/themeswitcher/components/ThemeComponent/ThemeComponent.css';

const classes = (classNames: (string | undefined | null | false)[]): string => {
  return classNames.filter(Boolean).join(' ');
};

export interface ThemeComponentProps extends JSX.HTMLAttributes<HTMLButtonElement> {
  children?: ComponentChildren;
}

export const ThemeComponent = ({
  className,
  children,
  ...props
}: ThemeComponentProps) => {
  const [isDarkTheme, setIsDarkTheme] = useState(false);

  useEffect(() => {
    const savedTheme = localStorage.getItem('theme');
    if (savedTheme) {
      setIsDarkTheme(savedTheme === 'dark');
    } else {
      const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
      setIsDarkTheme(prefersDark);
    }
  }, []);

  useEffect(() => {
    // Apply theme to the entire page
    const root = document.documentElement;
    const body = document.body;
    
    if (isDarkTheme) {
      root.classList.add('theme-dark');
      root.classList.remove('theme-light');
      root.setAttribute('data-theme', 'dark');
      body.style.backgroundColor = '#1a1a1a';
      body.style.color = '#ffffff';
    } else {
      root.classList.add('theme-light');
      root.classList.remove('theme-dark');
      root.setAttribute('data-theme', 'light');
      body.style.backgroundColor = '#ffffff';
      body.style.color = '#000000';
    }
    
    localStorage.setItem('theme', isDarkTheme ? 'dark' : 'light');
  }, [isDarkTheme]);

  const toggleTheme = () => {
    setIsDarkTheme(!isDarkTheme);
  };

  return (
    <button 
      {...props}
      className={classes(['theme-switcher-button', className as string])}
      onClick={toggleTheme}
      aria-label={`Switch to ${isDarkTheme ? 'light' : 'dark'} theme`}
    >
      {isDarkTheme ? '‚òÄÔ∏è' : 'üåô'} {isDarkTheme ? 'Light' : 'Dark'} Theme
    </button>
  );
};
