import { JSX } from 'preact';
import { ThemeComponent } from '../../components/ThemeComponent/ThemeComponent';

export interface ThemeContainerProps extends JSX.HTMLAttributes<HTMLDivElement> {}
    
export const ThemeContainer = ({ children, ...props }: ThemeContainerProps) => {
  return (
    <div {...props}>
      <ThemeComponent>
        {children}
      </ThemeComponent>
    </div>
  );
};
